.additionally {
  margin-bottom: 34px;

  @media (min-width: $tablet-width) {
    margin-bottom: 23px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 29px;
  }
}

.additionally ul {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.additionally li {
  position: relative;
  line-height: 30px;
  margin-bottom: 12px;


  @media (min-width: $tablet-width) {
    width: 220px;
    margin-right: 123px;
    margin-bottom: 32px;
    line-height: 40px;
  }

  @media (min-width: $desktop-width) {
    width: 220px;
    margin-right: 105px;
  }

  &:nth-child(2n) {
    @media (min-width: $tablet-width) {
      margin-right: 0;
    }

    @media (min-width: $desktop-width) {
      margin-right: 105px;
    }
  }

  &:nth-child(4n) {
    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }
}

.additionally__label {
  margin-left: 45px;
  cursor: pointer;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin-left: 59px;
  }
}

.additionally__checkbox + label::before {
  content: "";
  position: absolute;
  width: 28px;
  height: 28px;
  top: 1px;
  left: 1px;
  border: 1px solid #cdcdcd;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.additionally__checkbox:hover + label::before,
.additionally__checkbox:focus + label::before {
  border-color: #bababa;
}

.additionally__checkbox:checked + label::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 16px;
  top: 8px;
  left: 5px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    width: 18px;
    height: 14px;
    top: 14px;
    left: 12px;
  }
}
