.stats {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    width: 366px;
    flex-direction: column;
  }
}

.stats__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}

.stats__item {
  position: relative;
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    margin-right: 42px;
  }
}

.stats__value {
  padding: 13px 0;
  width: 124px;
  min-height: 56px;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  z-index: 1;

  @media (min-width: $tablet-width) {
    padding: 15px 0;
    width: 162px;
    min-height: 72px;
    font-size: 30px;
    line-height: 37px;
  }
}

.stats__category {
  position: absolute;
  padding: 0 10px;
  width: 87px;
  top: 48px;
  left: 17px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  color: #444444;
  background-color: #eaeaea;
  z-index: 2;

  @media (min-width: $tablet-width) {
    width: 117px;
    top: 64px;
    left: 22px;
    font-size: 14px;
    line-height: 14px;
    background-color: #ffffff;
  }

  @media (min-width: $desktop-width) {
    background-color: #eaeaea;
  }

  &--long {
    @media (min-width: $tablet-width) {
      width: 147px;
      left: 7px;
    }
  }
}

.stats__cost {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;

  @media (min-width: $tablet-width) {
    margin-top: 7px;
    font-size: 20px;
    line-height: 29px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 21px;
    text-align: left;
  }
}

.stats__sum {
  @media (min-width: $tablet-width) {
    display: block;
    text-align: right;
  }

  @media (min-width: $desktop-width) {
    display: inline;
    margin-left: 59px;
  }
}
