.advantages {
  @media (min-width: $desktop-width) {
    margin: 31px 0 0;
  }
}

.advantages__list {
  display: grid;
  margin: 0;
  margin-top: 40px;
  margin-bottom: 21px;
  padding: 0;
  list-style: none;


  @media (min-width: $tablet-width) {
    grid-template-columns: 324px 324px;
    column-gap: 60px;
    margin-top: 72px;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: auto auto auto auto;
    column-gap: 80px;
  }
}

.advantages__item {
  position: relative;
  margin-bottom: 26px;
  padding-left: 80px;

  @media (min-width: $tablet-width) {
    margin-right: 84px;
    margin-bottom: 37px;
    padding: 113px 40px 0 0;
    width: 300px;
    min-height: 280px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    padding: 113px 0 0;
    width: 245px;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 60px;
    height: 60px;
    top: -1px;
    left: 0;
    background: #68b738;

    @media (min-width: $tablet-width) {
      width: 80px;
      height: 80px;
      top: 0;
    }
  }

  &--img {
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 50px;
      height: 50px;
      top: 3px;
      left: 5px;
      background-repeat: no-repeat;
      z-index: 1;

      @media (min-width: $tablet-width) {
        top: 15px;
        left: 15px;
      }
    }
  }

  &--leaf {
    &::after {
      background-image: url("../img/advantage-leaf@1x.png");

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/advantage-leaf@2x.png");
        background-size: 50px auto;
      }
    }
  }

  &--powder {
    &::after {
      background-image: url("../img/advantage-powder@1x.png");

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/advantage-powder@2x.png");
        background-size: 50px auto;
      }
    }
  }

  &--dining {
    &::after {
      background-image: url("../img/advantage-dining@1x.png");

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/advantage-dining@2x.png");
        background-size: 50px auto;
      }
    }
  }

  &--clock {
    &::after {
      background-image: url("../img/advantage-clock@1x.png");

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/advantage-clock@2x.png");
        background-size: 50px auto;
      }
    }
  }

  &:nth-child(2n) {
    @media (min-width: $tablet-width) {
      margin-right: 0;
    }
  }
}

.advantages__description {
  margin: 0;
  font-family: "Arial", sans-serif;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }

  &--number {
    &::after {
      @media (min-width: $tablet-width) {
        content: "";
        display: block;
        position: absolute;
        width: 121px;
        height: 280px;
        top: 0;
        left: 125px;
        background-repeat: no-repeat;
        z-index: -1;
      }
    }
  }

  &--one {
    &::after {
      @media (min-width: $tablet-width) {
        background-image: url("../img/one.png");
      }
    }
  }

  &--two {
    &::after {
      @media (min-width: $tablet-width) {
        background-image: url("../img/two.png");
      }
    }
  }

  &--three {
    &::after {
      @media (min-width: $tablet-width) {
        background-image: url("../img/three.png");
      }
    }
  }

  &--four {
    &::after {
      @media (min-width: $tablet-width) {
        background-image: url("../img/four.png");
      }
    }
  }
}
